* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Noto Sans TC', sans-serif;
}
body::-webkit-scrollbar {
  width: 0.8rem;
  background-color: #171717;
}
body::-webkit-scrollbar-thumb {
  background: #4b4b4b;
  border-radius: 1rem;
}
a {
  color: #fff;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--primary-color);
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* -------- ------- ------ ----- Colors ----- ------ ------- -------- */

:root {
	/* --primary-color: #34b391;
	--secondary-color: #fadc94;
	--tertiary-color: #065446;
	--quaternary-color: #1f8b79;
	--border-card-color: #8f753a; */

	--primary-color: #e2e8f0;
	--secondary-color: #282840;
	--tertiary-color: #000;
	--quaternary-color: #fadc94;
	--border-card-color: #7c3aed;
}
.background {
	height: 8.5vh;
	/* margin-bottom: 8.5vh; */
	width: 100vw;
	background-color: #000;
	background-color: var(--tertiary-color);
	position: fixed;
	top: 0;
}
.app {
	color: white;
	max-width: 1250px;
	margin: 0 auto;
	position: relative;
	display: flex;
	flex-direction: column;
}
.logo {
	max-width: 2.5rem;
	max-height: 7.6vh;
	margin-right: 5px;
}

.subtitle {
	background-color: #000;
	background-color: var(--tertiary-color);
	/* line-height: 9vh; */
	padding: 0.2em;
	text-align: center;
	font-size: 2em;
}
@media (prefers-reduced-motion: no-preference) {
	.logo {
		-webkit-animation: App-logo-spin infinite 10s linear;
		        animation: App-logo-spin infinite 10s linear;
	}
}

@-webkit-keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@media screen and (max-width: 1000px) {
	.background {
		display: none;
	}
}

.navbar {
	display: flex;
	width: 100%;
	max-width: 1250px;
	justify-content: space-between;
	align-items: center;
	background-color: var(--tertiary-color);
	position: fixed;
	top: 0;
	z-index: 100;
	height: 8.5vh;
}
.navbar__left {
	display: flex;
	padding: 0.5rem;
	align-items: center;
	text-decoration: none;
	color: #fff;
	/* font-size: auto; */
}
.navbar__right {
	padding: 0.5rem;
}
.navbar__ul {
	display: flex;
	list-style: none;
}
.navbar__li {
	padding: 10px;
	line-height: 1.5rem;
	cursor: pointer;
	border-radius: 1px;
	font-weight: 700;
	border-bottom: 5px solid transparent;
	/* border-top: 5px solid transparent; */
}
.navbar__li:hover {
	/* background-color: var(--quaternary-color); */
	/* color: black; */
	border-bottom: 5px solid var(--border-card-color);
	/* border-top: 5px solid var(--border-card-color); */
}

@media screen and (max-width: 1000px) {
	.navbar {
		border-radius: 0;
		width: 100vw;
	}
}

@media screen and (max-width: 600px) {
	.navbar__right {
		display: none;
	}
}

.main {
	margin-top: 17vh;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-auto-rows: -webkit-min-content;
	grid-auto-rows: min-content;
	padding: 1em;
	/* color: #000; */
	text-align: center;
	grid-column-gap: 5em;
	-webkit-column-gap: 5em;
	        column-gap: 5em;
	/* row-gap: 1em; */
	height: 50vh;
	-webkit-animation-name: appear;
	        animation-name: appear;
	-webkit-animation-duration: 0.8s;
	        animation-duration: 0.8s;
}

@-webkit-keyframes appear {
	0% {
		opacity: 0;
		transform: translateY(-50vh);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

@keyframes appear {
	0% {
		opacity: 0;
		transform: translateY(-50vh);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

.main > div {
	background-color: var(--secondary-color);
	/* Created with https://www.css-gradient.com */
	/* Created with https://www.css-gradient.com */
	/* background: linear-gradient(
		180deg,
		rgba(41, 41, 93, 1) 22%,
		rgba(30, 30, 92, 1) 82%
	); */
	border-radius: 5px;
	border-top: 5px solid var(--border-card-color);
	padding: 0.8em;
}
.stack {
	grid-row: 1 / 3;
	grid-column: 3 / 4;
}
.description__text {
	margin: 1.5em 0 2.6em;
	text-align: justify;
}
.main__container-foto {
	position: relative;
	top: 6em;
	display: flex;
	align-items: center;
	flex-direction: column;
	margin: 0 -1.5em;
}
.main__name {
	font-size: 2.1em;
	margin: 0.2em 0;
}
.main__photo {
	border-radius: 9rem;
	border: 13px solid var(--primary-color);
	max-width: 70%;
	height: 265px;
	position: absolute;
	top: 8.3em;
	object-fit: cover;
	/* left: ; */
}
.stack {
	overflow: hidden;
	/* padding-bottom: 1000px; */
}
.stack__technologies {
	margin: 1.5em 0 0.5em;
	max-width: 100%;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-auto-rows: 4em;
	transition: transform 0.2s;
	grid-gap: 0.8em;
	gap: 0.8em;
}
.stack__technologie:hover {
	/* filter: drop-shadow(0 20px 13px rgb(255 255 255 / 0.1))
		drop-shadow(0 8px 5px rgb(255 255 255 / 0.03)); */
	/* background-color: #dc3545; */
	transform: scale(1.1);
	/* transform: rotate(5deg); */
}

.stack__img {
	width: 100%;
	height: 100%;
	object-fit: contain;
	cursor: pointer;
	-webkit-animation-name: stack__img-appear;
	        animation-name: stack__img-appear;
	-webkit-animation-duration: 1.6s;
	        animation-duration: 1.6s;
	-webkit-animation-fill-mode: both;
	        animation-fill-mode: both;
	opacity: 0;
}

@-webkit-keyframes stack__img-appear {
	50% {
		/* background: transparent; */
	}
	75% {
		/* background-color: #000; */
	}
	100% {
		opacity: 1;
		/* background-color: transparent; */
	}
}

@keyframes stack__img-appear {
	50% {
		/* background: transparent; */
	}
	75% {
		/* background-color: #000; */
	}
	100% {
		opacity: 1;
		/* background-color: transparent; */
	}
}

.stack__description {
	margin: 0 8px;
}
.active {
	border: 5px double var(--border-card-color);
	border-top: none;
	border-left: none;
	grid-column: 1 / span 4;
	grid-row: 1 / span 2;
	display: flex;
	justify-content: space-evenly;
	position: relative;
	border-radius: 3px;
	padding: 0.5em;
	margin-bottom: 0.5em;
	text-align: justify;
	font-size: 0.85em;
	overflow: auto;
	/* font-family: ; */
}
.active::-webkit-scrollbar {
	display: none;
}
.active > .stack__img {
	width: auto;
	max-width: 30%;
	height: 70%;
	position: -webkit-sticky;
	position: sticky;
	top: 0px;
}
.desactive {
	/* padding: 3px 8px; */
	/* margin-left: 5px; */
	/* background: #000; */
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	right: 0;
	color: #dc3545;
	border: none;
	/* border-radius: 2px; */
	cursor: pointer;
	align-self: flex-start;
	font-size: 1.4em;
	/* font-weight: 700; */
}
.desactive:hover {
	transform: scale(1.12);
}

#grad1 {
	height: 200px;
	background: red; /* For browsers that do not support gradients */ /* For Safari 5.1 to 6.0 */ /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
	background: linear-gradient(
		to right,
		orange,
		yellow,
		green,
		cyan,
		blue,
		violet
	); /* Standard syntax (must be last) */
}

@media screen and (max-width: 1600px) {
	.main {
		margin-bottom: 12em;
	}
}

@media screen and (max-width: 1000px) {
	.main {
		grid-template-columns: 1fr 1fr;
		max-width: 100vw;
		margin: 15vh auto 5vh;
		height: auto;
		grid-column-gap: 1em;
		-webkit-column-gap: 1em;
		        column-gap: 1em;
		grid-row-gap: 1em;
		row-gap: 1em;
	}
	.stack {
		grid-row: initial;
		grid-column: initial;
	}
	.main__container-foto {
		grid-row: 1 / 3;
		top: 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin: 0;
	}
	.main__name {
		margin-top: 0;
		margin-bottom: 3vh;
	}
	.main__photo {
		top: 0;
		max-width: 260px;
		position: static;
		position: initial;
		/* left: 10%; */
	}
	.stack__technologies {
		grid-template-columns: repeat(3, 1fr);
		margin: 1.5em 0;
		grid-gap: 0.5em;
		gap: 0.5em;
	}
	.active {
		grid-column: 1 / span 2;
	}
}

@media screen and (max-width: 650px) {
	.main {
		grid-template-columns: 1fr;
		max-width: 100%;
		font-size: 20px;
		border-radius: 0;
		background-color: transparent;
		grid-row-gap: 0.8em;
		row-gap: 0.8em;
		margin: 0;
		margin-top: 9vh;
	}
	.main > div {
		padding: 0.5em;
		border-radius: 5px;
	}
	.main__container-foto {
		grid-row: 1 / 2;
	}
	.stack__technologies {
		grid-template-columns: repeat(3, 1fr);
	}
	.active {
		grid-column: 1 / span 3;
	}
}

.projects {
	max-width: 100%;
	/* margin-block: ; */
	margin: 10vh 0;
}

.projects-grid {
	max-width: 98%;
	margin: 8vh auto 0;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	/* gap: 0.15em; */
	justify-items: center;
}

.projects__card {
	border-radius: 6px;
	padding: 0;
	max-width: 85%;
	height: auto;
	display: flex;
	flex-direction: column;
	background-color: #fff;
	overflow: hidden;
	transition: transform 0.2s;
	/* border: 2px solid var(--border-card-color); */
	box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
	background-color: var(--secondary-color);
}

.projects__card:hover {
	transform: scale(1.2);
}

.projects__img {
	width: 100%;
	/* max-height: 70%; */
	height: 100%;
	object-fit: fill;
}
.projects__description {
	/* color: #000; */
	display: flex;
	min-height: 15vh;
	text-align: center;
	justify-content: center;
	padding: 1em;
	font-weight: 700;
	font-size: 1.2em;
	align-items: center;
}
.projects__button-container {
	display: flex;
	justify-content: center;
	margin: 8vh auto 0;
}
.projects__button {
	padding: 0.7rem 5rem;
	border-radius: 5px;
	background-color: transparent;
	border: 3px solid var(--secondary-color);
	transition: all 200ms;
	font-weight: 700;
	font-size: 1.5em;
	color: var(--secondary-color);
	outline: none;
}
.projects__button:hover {
	background-color: var(--secondary-color);
	border-color: #000;
	color: #fff;
	cursor: pointer;
	transform: scale(1.1);
}

@media screen and (max-width: 1000px) {
	.projects-grid {
		max-width: 80%;
		grid-template-columns: 1fr 1fr;
		margin: 10vh auto 5vh;
		grid-gap: 2em;
		gap: 2em;
	}
	.projects__card {
		max-width: 100%;
	}
	.projects__card:hover {
		transform: scale(1.1);
	}
	.projects__button {
		margin-top: 5vh;
	}
}

@media screen and (max-width: 600px) {
	.projects {
		margin-top: 0;
	}
	.projects-grid {
		margin: 5vh auto;
		grid-template-columns: 1fr;
		max-width: 82%;
	}
	.projects__card:hover {
		transform: none;
	}
	.projects__description {
		font-size: 1.4em;
	}
	.projects__button {
		margin-top: 0vh;
	}
}

.form-section {
	/* background-color: var(--secondary-color); */
	/* border-top: 6px solid var(--border-card-color); */
	/* border-top: none; */
	margin: 6vh 1vw 10vh;
	/* padding: 10px; */
	border-radius: 5px;
	display: flex;
	justify-content: space-around;
	position: relative;
	-webkit-filter: drop-shadow(0 25px 25px rgb(0 0 0 / 0.15));
	        filter: drop-shadow(0 25px 25px rgb(0 0 0 / 0.15));
}
.form-section__img-credits {
	position: absolute;
	right: 0;
	bottom: 0;
	margin-right: 10px;
	font-size: 10px;
	opacity: 0.1;
}
.form-section__img {
	margin-right: 3%;
	max-width: 38%;
	object-fit: contain;
}
.form {
	background-color: var(--secondary-color);
	/* background: linear-gradient(
		180deg,
		rgba(41, 41, 93, 1) 22%,
		rgba(24, 24, 94, 1) 82%
	); */
	border-top: 6px solid var(--border-card-color);
	display: flex;
	flex-direction: column;
	padding: 1.5em;
	margin-right: 9%;
	width: 45%;
	border-radius: 3px;
	font-weight: 700;
}
.form__label {
	/* padding: 1em; */
	/* display: block; */
	cursor: pointer;
}
.form__p {
	font-size: 1.1rem;
	/* margin-left: px; */
}
.form__input {
	margin: 0.2rem 0 1rem;
	padding: 0.3rem;
	background: transparent;
	border: none;
	width: 100%;
	border-bottom: 3px solid var(--border-card-color);
	outline: none;
	resize: none;
	font-size: inherit;
	color: #fff;
}
.form__input:focus {
	/* background-color: #0002; */
	background: linear-gradient(to bottom right, #0001, #0004);
}
.form__textarea {
	height: 7em;
}
.form__div-feedback {
	text-align: center;
	border-radius: 5px;
	font-size: 0.9rem;
	font-weight: 300;
	padding: 3px 15px;
	margin-bottom: 0.5rem;
	width: auto;
	align-self: center;
	display: none;
}
.form__div-correct {
	display: block;
	background: #00a100c2;
}
.form__div-incorrect {
	display: block;
	background: #ff0000c2;
}
.form__submit {
	font-weight: inherit;
	font-size: inherit;
	margin: 0.5em;
	padding: 0.7em;
	background: transparent;
	background-color: var(--primary-color);
	border: 3px solid var(--tertiary-color);
	border-radius: 5px;
	cursor: pointer;
	transition: background 200ms transform 100ms;
	outline: none;
	/* max-width: 50%; */
}
.form__submit:hover,
.form__submit:focus {
	/* border-color: #000; */
	transform: scale(0.97);
	/* color */
}

@media screen and (max-width: 1000px) {
	.form {
		max-width: 100%;
	}
	.form__div-feedback {
		font-size: 0.7rem;
	}
}

@media screen and (max-width: 600px) {
	.form-section {
		flex-direction: column-reverse;
		margin: 5vh 4vw 10vh;
		/* flex-flow: column-reverse; */
	}
	.form-section__img {
		max-width: 100%;
		margin-right: 0;
	}
	.form {
		width: 100%;
	}
}

